          cycle: 3,
          startTime: "2018-06-01",
          endTime: "2018-06-30",
          isCount: true,
          groupBys: "0001"
        }
      }).then(res => {
        if (res.data.status) {
          let data = res.data.records;
          let obj = {
            num: 1,
            dataArray: [data],
            relation: [],
            uniqueField: ["dataTime"],
            column: [
              {
                field: "dataTime",
                formula: "",
                children: null
              },
              {
                field: "meterId",
                formula: "OPEN([meterId])",
