<template>
    <el-form :model="dbForm" label-width="80px">
        <el-form-item prop="source" v-if="type===1">
            <el-select v-model="dbForm.source">
                <el-option v-for="item in dbSourceList" :key="item.id" :label="item.name" :value="item.id"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item v-if="type===1" prop="sourceValue">
            <el-input v-model="dbForm.expression" :rows="6" type="textarea"></el-input>
        </el-form-item>
        <el-form-item prop="source" v-if="type===2" label="é€‰æ‹©æŽ¥å£">
            <el-select v-model="dbForm.source" value-key="id">
                <el-option v-for="item in dbInterfaceList" :key="item.id" :label="item.name" :value="item"></el-option>
            </el-select>
        </el-form-item>
        <div v-if="type===2">æŽ¥å£å‚æ•°</div>
        <el-form-item v-if="type===2" prop="sourceValue" v-for="item in dbForm.source.list" :key="item.id" ï¼›:label="item.name">
            <el-input v-model="dbForm[item.value]"></el-input>
        </el-form-item>
    </el-form>
</template>
<script>
export default {
  props: {
    value: {
      type: [Object, Boolean]
    },
    type: {
      default: [Number, String]
    }
  },
  data() {
    return {
      dbSourceList: [],
      dbInterfaceList: [],
      dbForm: {
        source: "",
        expression: ""
      }
    };
  },
  watch: {
    dbForm: {
      handler(valueObj) {
        this.$emit("input", valueObj);
      },
      deep: true
    }
  },
  methods: {
    resetData() {
      this.dbForm = {
        source: "",
        expression: ""
      };
    },
    getDBSourse() {
      this.$ajax("rest/target/targetDbSource").then(res => {
        this.dbSourceList = res.data.records;
      });
    },
    getTargetInterface() {
      this.$ajax
        .get("rest/target/targetInterface", { params: {} })
        .then(res => {
          this.$nextTick(() => {
            this.dbInterfaceList = res.data.records;
          });
        });
    },
    getData() {
      this.$nextTick(() => {
        this.type === 1 ? this.getDBSourse() : this.getTargetInterface();
      });
    },
    // æ ¡éªŒè¡¨è¾¾å¼å‡†ç¡®æ€§
    checkExpression() {
      return this.dbForm;
      //   form.valueSource = this.expressionForm.expressionTrue;
      //   this.$ajax.post("", form).then(res => {
      //     if (res.data.status) {
      //       this.$emit("input", this.dbForm);
      //     } else {
      //       this.$message.error(res.data.answerMsg);
      //     }
      //   });
    }
  },
  updated() {
    // this.getData();
  },
  mounted() {
    // this.getDBSourse();
    // this.getTargetInterface();
  }
};
</script>



